* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  background: #122f41;
}

body.locked {
  overflow: hidden;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-right: 15px;
  padding-left: 15px;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

::before,
::after {
  font-family: "fontawesome";
}

.burger::before {
  content: "\f805";
  display: inline-block;
  font-size: 32px;
  position: fixed;
  top: 30px;
  right: 30px;
  z-index: 10;
  color: #fff;
  display: none;
}

@media (max-width: 992px) {
  .burger::before {
    display: inline-block;
  }
}

.cancel::before {
  content: '\f410';
}

.wrapper::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  opacity: 0.9;
  z-index: 998;
  display: none;
}

.wrapper.active::before {
  display: block;
}

.slider {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transition: 1s;
}

.slider.active {
  opacity: 1;
  visibility: visible;
}

.slider-slide {
  width: 100%;
  height: 100%;
}

.black {
  background: #000;
}

.intro {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: 1s;
  padding: 0 10px;
}

.intro.slide {
  opacity: 0;
  visibility: hidden;
  top: -100%;
}

.intro__uptitle {
  font-family: "Maximian";
  text-transform: uppercase;
  font-size: clamp(42px, calc(71vw/14.4), 80px);
  line-height: clamp(62px, calc(100vw/14.4), 110px);
  background: linear-gradient(180deg, rgba(12, 11, 11, 0.2) 0%, rgba(12, 11, 11, 0) 100%), linear-gradient(180deg, #00FDEE 0%, rgba(0, 253, 238, 0) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.intro__img {
  width: 100%;
  height: clamp(100px, calc(200vw/14.4), 250px);
  max-height: 100%;
  object-fit: cover;
}

.intro__button {
  text-transform: uppercase;
  font-family: "Rapidtech";
  font-size: clamp(42px, calc(52vw/14.4), 60px);
  line-height: clamp(42px, calc(52vw/14.4), 60px);
  text-align: center;
  color: #fff;
  background: linear-gradient(180deg, #13124A -31.23%, #1FF7F7 50.68%, #1B1A58 122.85%);
  border-radius: 25px;
  border: none;
  padding: 15px 20px;
  cursor: pointer;
}

.pro {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
}

.pro__content {
  width: 50%;
  height: 100%;
  background: #2f89e3;
  background: linear-gradient(100deg, #2f89e3 0%, rgba(47, 137, 227, 0.3) 70%, rgba(18, 47, 65, 0.7) 90%, #122f41 100%);
  -webkit-box-shadow: -29px -17px 51px 0px #122f41 inset;
  -moz-box-shadow: -29px -17px 51px 0px #122f41 inset;
  box-shadow: -29px -17px 51px 0px #122f41 inset;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 1200px) {
  .pro__content {
    width: 35%;
  }
}

@media (max-width: 992px) {
  .pro__content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 20%;
  }
}

.pro__content-border {
  width: clamp(30px, calc(50vw/14.4), 60px);
  height: 150%;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: absolute;
  top: -10%;
  right: 0;
  transform: rotate(15deg);
}

@media (max-width: 1200px) {
  .pro__content-border {
    transform: rotate(6deg);
  }
}

@media (max-width: 992px) {
  .pro__content-border {
    display: none;
  }
}

.pro__content-border-part {
  flex: auto;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  border-left: 5px solid #000;
  border-right: 5px solid #000;
}

.pro__desc {
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (max-width: 1200px) {
  .pro__desc {
    width: 55%;
  }
}

@media (max-width: 992px) {
  .pro__desc {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 80%;
    text-align: center;
    padding-top: 1%;
  }
}

.pro__desc-title {
  font-family: "RedRose";
  font-weight: 700;
  font-size: clamp(32px, calc(72vw/14.4), 80px);
  line-height: clamp(40px, calc(82vw/14.4), 90px);
  border-bottom: 2px solid #fff;
  color: #fff;
}

.pro__desc-list {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: space-evenly;
}

.pro__desc-list li {
  display: flex;
  align-items: center;
  gap: 20px;
}

.pro__desc-list li span {
  font-family: "serif";
  font-size: clamp(14px, calc(40vw/14.4), 45px);
  line-height: clamp(20px, calc(50vw/14.4), 55px);
  color: #fff;
}

@media (max-width: 992px) {
  .pro__desc-list {
    padding: 0 20px;
    gap: 20px;
  }
}

@media screen and (orientation: landscape) {
  .pro__desc-list {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.pro__desc-img {
  width: clamp(30px, calc(50vw/14.4), 60px);
  height: clamp(30px, calc(50vw/14.4), 60px);
  object-fit: contain;
}

.pro__desc-subtitle {
  font-family: "Kurale";
  font-size: clamp(18px, calc(40vw/14.4), 50px);
  line-height: clamp(24px, calc(60vw/14.4), 70px);
  color: #fff;
}

.pro__img {
  width: 400px;
  height: 500px;
  position: absolute;
  top: -15%;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

.pro__img img {
  padding-top: 10%;
  width: 100%;
  height: 100%;
  display: block;
  margin: 0 auto;
  border: none;
  object-fit: cover;
}

@media (max-width: 992px) {
  .pro__img {
    height: 110%;
    width: clamp(100px, calc(150vw/14.4), 170px);
    inset: 0;
  }
}

.nav__list {
  position: fixed;
  z-index: 999;
  left: 0;
  height: clamp(30px, calc(60vw/14.4), 100px);
  width: 100%;
  top: -100%;
  transition: 0.5s;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 576px) {
  .nav__list {
    width: 100%;
    overflow: scroll;
  }
}

@media (max-width: 768px) {
  .nav__list {
    flex-direction: column;
    justify-content: space-between;
    width: 50%;
    height: 100%;
    background: #fff;
    align-items: start;
    padding-left: 10%;
    top: 0;
    left: -100%;
  }
  .nav__list.active {
    left: 0;
  }
}

.nav__list.active {
  top: 0;
}

.nav__item {
  flex: auto;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #000;
  background: #fff;
}

@media (max-width: 768px) {
  .nav__item {
    border: none;
    transform: translateX(-60px);
    opacity: 0;
    transition: 1s;
  }
  .nav__item.active {
    transform: translateX(0);
    opacity: 1;
  }
}

.nav__link {
  font-family: "Gochi";
  font-size: clamp(24px, calc(40vw/14.4), 50px);
  line-height: clamp(30px, calc(46px,56px));
  color: #000;
}

.nav__logo {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.nav__lang {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.nav__title {
  position: fixed;
  top: 0;
  left: 5%;
  padding: 5px clamp(5px, calc(10vw/14.4), 15px);
  font-size: clamp(18px, calc(24vw/14.4), 32px);
  line-height: clamp(20px, calc(36vw/14.4), 40px);
  background: #fff;
  font-family: "Gochi";
  border-bottom-right-radius: clamp(10px, calc(20vw/14.4), 30px);
  border-bottom-left-radius: clamp(10px, calc(20vw/14.4), 30px);
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  transition: 0.6s;
}

.nav__title.active {
  opacity: 1;
  visibility: visible;
}

.nav__cancel {
  font-size: 34px;
  position: absolute;
  top: 10px;
  right: 10px;
  display: none;
}

@media (max-width: 500px) {
  .nav__cancel {
    display: inline-block;
  }
}

